<template>
  <div class="cms-list-layout">
    <cms-prompt collapse='true'>
      <li>显示系统使用的物流公司列表</li>
      <li>可对物流公司列表进行新增、修改、删除操作</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>模板名称</label>
        <el-input v-model="queryParams.cnName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <cms-sticky>
    <section class="cms-batch">
      <cms-button v-prem="'/physicalCompany/save'"  icon="add" type="success" size="small" @click="$routerLink('/physicalCompany/save','save')">新增</cms-button>
      <cms-button v-prem="'/physicalCompany/delete'" icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
    </section>
    </cms-sticky>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="cnName" label="模板名称" min-width='120'></el-table-column>
      <el-table-column prop="initialName" label="快递公司" min-width="160"></el-table-column>
      <el-table-column label="LOGO" min-width="180" prop="logo">
        <div slot-scope="scope" class="table-img">
          <img v-if="scope.row.logoImgResourceData" :src="$cms.filterUrl(scope.row.logoImgResourceData.url) " alt="mockimg">
        </div>
      </el-table-column>
       <el-table-column label="排序" prop="sortNum">
        <template slot-scope="scope">
          <el-input v-model="scope.row.sortNum" @keyup.enter.native="sortNum($event,scope.row.id)"> </el-input>
        </template>
      </el-table-column>
      <el-table-column prop="isEnable" label="是否启用" width="110">
        <div slot-scope="scope">
          <cms-switch v-model="scope.row.isEnable" :id='scope.row.id' @change="setDisable"></cms-switch>
        </div>
      </el-table-column>
      <el-table-column label="操作" width='175' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button v-prem="'/physicalCompany/edit'"  icon='edit' size='small' type='info' @click.stop="$routerLink('/physicalCompany/edit','update',{id:scope.row.id})">编辑</cms-button>
          <cms-button v-prem="'/physicalCompany/delete'" icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  logisticsCompanyPage,
  logisticsCompanyDelete,
  logisticsCompanySort,
  logisticsCompanyEnable,
  logisticsCompanyDisable
} from '@/api/setting/logisticsCompany.js'
var defaultData = {
  cnName: '',
  size: 10,
  page: 1
}
export default {
  name: 'logisticsCompanyList',
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(logisticsCompanyDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(logisticsCompanyDelete, this.ids) // 这个才是真实的删除方法
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.startTime = timer[0]
      this.queryParams.endTime = timer[1]
      this.query()
    },
    sortNum(el, id) {
      // 数字排序
      const val = el.target.value
      const reg = /^[1-9][0-9]{0,1}$/
      if (val === '') {
        return false
      } else {
        if (reg.test(val)) {
          el.target.className = 'el-input__inner'
          logisticsCompanySort({ sorts: [{
            id: id,
            sortNum: val
          }] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('修改成功')
            } else {
              this.$errorMessage('修改失败')
            }
          })
        } else {
          el.target.className = 'cms-error el-input__inner'
          this.$cmsMessage({
            message: '只能输入1-99的正整数',
            type: 'error',
            timer: '1000',
            fastClick: false
          })
        }
      }
    },
    setDisable(status, id) {
      if (status) {
        logisticsCompanyEnable({ ids: [id] }).then(res => {
          // 启用
          if (res.code === 200) {
            this.$successMessage('启用成功')
          } else {
            this.$errorMessage('启用失败')
          }
        })
      } else {
        logisticsCompanyDisable({ ids: [id] }).then(res => {
          // 禁用
          if (res.code === 200) {
            this.$successMessage('禁用成功')
          } else {
            this.$errorMessage('禁用失败')
          }
        })
      }
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(logisticsCompanyPage, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(logisticsCompanyPage, this.queryParams)
  }
}
</script>

<style>
</style>



